import { Vue, Component, Prop, Model, Watch } from "vue-property-decorator";
import AddSysuser from "./components/AddSysuser.vue";
import SystemUserInfo from "./components/SystemUserInfo.vue";
import UpdateInfo from "./components/UpdateInfo.vue";
import AffiliateTeam from "./components/AffiliateTeam.vue";

import Url from "../../api";
import utils from "@/utils/utils";

@Component({
  components: {
    AddSysuser,
    SystemUserInfo,
    UpdateInfo,
    AffiliateTeam
  }
})
export default class SystemUserManage extends Vue {
  tableForm = {
    realName: "",
    username: "",
    mobile: "",
    locked: ""
  };
  loading = false;
  tableData = [];
  total = 0;
  pageNum = 1;
  pageSize = 10;

  dialogTitle = "";
  visibleDialog = false;
  formData = {
    id: "",
    realName: "",
    gender: "男",
    birthDay: "",
    username: "",
    mobile: "",
    password: "",
    remark: ""
  };

  currentId = "";
  infoDialog = false;

  dialogInfoTitle = "";
  visibleInfoDialog = false;
  infoValue = "";
  typeUpdate = 1;

  authTeamDialog = false;

  getTableData() {
    this.loading = true;
    this.$http
      .post(Url.getSystemUser, {
        query: this.tableForm,
        current: this.pageNum,
        size: this.pageSize
      })
      .then((res: any) => {
        this.loading = false;
        if (res.data.status) {
          this.tableData = res.data.data.records;
          this.total = res.data.data.total;
        } else {
          this.$message.error(res.data.message);
        }
      })
      .catch(() => {
        this.loading = false;
      });
  }

  search() {
    this.pageNum = 1;
    this.getTableData();
  }

  handleCurrentChange(val: number) {
    this.pageNum = val;
    this.getTableData();
  }
  handleSizeChange(val: number) {
    this.pageSize = val;
    this.getTableData();
  }

  handleAdd() {
    this.dialogTitle = "添加系统用户";
    this.formData = {
      id: "",
      realName: "",
      gender: "男",
      birthDay: "",
      username: "",
      mobile: "",
      password: "",
      remark: ""
    };
    this.visibleDialog = true;
  }
  handleEdit(row: any) {
    this.dialogTitle = "编辑系统用户";
    utils.mapValue(this.formData, row);
    this.visibleDialog = true;
  }
  handleAddSubmit() {
    this.search();
    this.visibleDialog = false;
  }
  // handleDelete(item: any) {
  //   let _this = this;
  //   this.$confirm("确认删除？", "提示", {
  //     confirmButtonText: "确定",
  //     cancelButtonText: "取消",
  //     type: "warning"
  //   })
  //     .then(() => {
  //       this.$http.get(Url.deleteSystemUser + "/" + item.id).then((res: any) => {
  //         if (res.status) {
  //           if (res.data.status) {
  //             this.$message({
  //               type: "success",
  //               message: "删除成功!"
  //             });
  //             //重新获取列表
  //             _this.getTableData();
  //           } else {
  //             _this.$message({
  //               showClose: true,
  //               message: res.data.message,
  //               type: "error"
  //             });
  //           }
  //         }
  //       });
  //     })
  //     .catch(() => {});
  // }
  closeDialog() {
    this.visibleDialog = false;
  }

  handleInfo(row: any) {
    this.currentId = row.id;
    this.infoDialog = true;
  }
  closeInfoDialog() {
    this.infoDialog = false;
  }

  handleEditPass(row: any, flag: number) {
    this.currentId = row.id;
    this.typeUpdate = flag;
    switch (this.typeUpdate) {
      case 1:
        this.dialogInfoTitle = "编辑用户名";
        this.infoValue = row.username;
        break;
      case 2:
        this.dialogInfoTitle = "编辑手机号";
        this.infoValue = row.mobile;
        break;
      case 3:
        this.dialogInfoTitle = "编辑密码";
        this.infoValue = "";
        break;
    }
    this.visibleInfoDialog = true;
  }
  closePassDialog() {
    this.visibleInfoDialog = false;
  }
  // 修改用户名，手机号，密码
  handleAddInfoSubmit() {
    this.visibleInfoDialog = false;
    this.getTableData();
  }

  //冻结-解冻
  handleLock(row: any) {
    let message = row.locked ? "解冻" : "冻结";
    this.$confirm("确定" + message + "该账号？", "提示", {
      confirmButtonText: "确定",
      cancelButtonText: "取消",
      type: "warning"
    })
      .then(() => {
        this.$http
          .post(Url.lockSysUser + "/" + row.id, null, {
            params: {
              locked: !row.locked
            }
          })
          .then((res: any) => {
            if (res.data.status) {
              this.$message.success("操作成功");
              this.getTableData();
            } else {
              this.$message.error(res.data.message);
            }
          });
      })
      .catch(() => {});
  }

  // 所属团队
  handleTeam(row: any) {
    this.currentId = row.id;
    this.authTeamDialog = true;
  }
  closeAuthTeamDialog() {
    this.authTeamDialog = false;
  }

  created() {
    this.getTableData();
  }
}
