




































































































































/**
 *
 * @author fqq
 */
import { Vue, Component, Prop, Model, Watch } from "vue-property-decorator";

import Url from "../../../api";

@Component({
  components: {}
})
export default class AddSysuser extends Vue {
  @Prop({ type: String, default: "添加系统用户" })
  dialogTitle?: string;

  @Prop({ type: Boolean, default: false })
  dialogVisible!: boolean;

  @Prop({ type: Object, default: () => ({}) })
  data!: any;

  @Watch("dialogVisible")
  handleVisible(newValue: boolean) {
    this.userDialog = newValue;
    if (newValue) {
      this.$nextTick(() => {
        let ref: any = this.$refs.addForm;
        ref.resetFields();
        this.addForm = JSON.parse(JSON.stringify(this.data));
      });
    }
  }

  userDialog = false;
  addForm: any = {
    id: "",
    realName: "",
    gender: "男",
    birthDay: "",
    username: "",
    mobile: "",
    password: "",
    tenantId: "",
    tenantName: "",
    remark: ""
  };
  loading = false;

  addDialog = false;
  teamName = "";
  teamCode = "";
  teamData: Array<any> = [];
  teamPageNum = 1;
  teamPageSize = 10;
  teamTotal = 0;
  teamId = "";

  handleSelectTeam() {
    this.teamName = "";
    this.teamCode = "";
    this.teamPageNum = 1;
    this.teamPageSize = 10;
    this.getTeamData();
    this.addDialog = true;
  }

  teamSearch() {
    this.teamPageNum = 1;
    this.getTeamData();
  }

  getTeamData() {
    this.$http
      .post(Url.getAllTeamList, {
        query: {
          tenantCode: this.teamCode,
          tenantName: this.teamName
        },
        current: this.teamPageNum,
        size: this.teamPageSize
      })
      .then((res: any) => {
        if (res.data.status) {
          this.teamData = res.data.data.records;
          this.teamTotal = res.data.data.total;
        } else {
          this.$message.error(res.data.message);
        }
      })
      .catch(() => {});
  }

  handleTeamCurrentChange(val: number) {
    this.teamPageNum = val;
    this.getTeamData();
  }
  handleTeamSizeChange(val: number) {
    this.teamPageSize = val;
    this.getTeamData();
  }

  handleAddTeam() {
    if (this.teamId === "") {
      this.$message.info("请先选择团队");
      return;
    }
    this.addForm.tenantId = this.teamId;
    let index: number = this.teamData.findIndex((li: any) => li.id === this.teamId);
    this.addForm.tenantName = this.teamData[index].name;
    this.addDialog = false;
  }

  saveSensorAttribute() {
    let rest: any = this.$refs.addForm;
    rest.validate((valid: any) => {
      if (valid) {
        // 添加需要选择所属团队，编辑不需要
        if (!this.addForm.id && !this.addForm.tenantId) {
          this.$message.error("请选择所属团队");
          return;
        }
        this.loading = true;
        let url = this.addForm.id ? Url.updateSystemUser : Url.addSystemUser;
        if (typeof this.addForm.remark === "string") {
          this.addForm.remark = this.addForm.remark.trim();
        }
        this.$http
          .post(url, this.addForm)
          .then((res: any) => {
            this.loading = false;
            if (res.data.status) {
              this.$message.success(this.addForm.id ? "编辑成功" : "添加成功");
              this.teamId = "";
              this.$emit("submit");
              let name = localStorage.getItem("userName");
              if (name === this.addForm.username) {
                localStorage.setItem("realName", this.addForm.realName);
                qTouch.emit("changeName", this.addForm.realName);
              }
            } else {
              this.$message.error(res.data.message);
            }
          })
          .catch(() => {
            this.loading = false;
          });
      } else {
        return false;
      }
    });
  }

  closeDialog() {
    this.$emit("close");
  }
}
