 *
 * @author fqq
 */
import { Vue, Component, Prop, Model, Watch } from "vue-property-decorator";

import Url from "../../../api";
import { validateTel } from "@/utils/validate";

@Component({
  components: {}
})
export default class UpdateInfo extends Vue {
  @Prop({ type: String, default: "修改信息" })
  dialogTitle?: string;

  @Prop({ type: Boolean, default: false })
  dialogVisible!: boolean;

  @Prop({ type: String, default: () => ({}) })
  data!: string;

  @Prop({ type: Number, default: () => ({}) })
  type!: number;

  @Prop({ type: String, default: "" })
  id!: string;

  @Watch("dialogVisible")
  handleVisible(newValue: boolean) {
    this.addDialog = newValue;
    if (newValue) {
      this.$nextTick(() => {
        let ref: any = this.$refs.addForm;
        ref.resetFields();
        this.addForm.value = this.data;
      });
    }
  }

  addDialog = false;
  addForm: any = {
    value: ""
  };
  loading = false;

  closeDialog() {
    this.$emit("close");
  }

  saveSensorAttribute() {
    let rest: any = this.$refs.addForm;
    rest.validate((valid: any) => {
      if (valid) {
        if (this.type === 3 && (this.addForm.value.length < 6 || this.addForm.value.length > 18)) {
          this.$message.warning("密码长度为6-18位");
          return;
        }
        if (this.type === 2 && !validateTel(this.addForm.value)) {
          this.$message.warning("手机号格式错误");
          return;
        }
        // if (this.type === 1 && !validateUserName(this.addForm.value)) {
        //   this.$message.warning("用户名只能包含英文字母、数字、下划线和减号，且不能为纯数字，长度6-18位");
        //   return;
        // }
        this.$confirm("确定修改？", "提示", {
          confirmButtonText: "确定",
          cancelButtonText: "取消",
          type: "warning"
        })
          .then(() => {
            this.loading = true;
            let url = "";
            let param: any = {};
            switch (this.type) {
              case 1:
                url = Url.updateName;
                param.username = this.addForm.value;
                break;
              case 2:
                url = Url.updateMobile;
                param.mobile = this.addForm.value;
                break;
              case 3:
                url = Url.updatePass;
                param.password = this.addForm.value;
                break;
            }
            this.$http
              .post(url + "/" + this.id, null, {
                params: param
              })
              .then((res: any) => {
                this.loading = false;
                if (res.data.status) {
                  this.$message.success("修改成功");
                  this.$emit("submit");
                } else {
                  this.$message.error(res.data.message);
                }
              })
              .catch(() => {
                this.loading = false;
              });
          })
          .catch(() => {});
      } else {
        return false;
      }
    });
  }
}
